import React from 'react';
import './IpInfo.css';

export const IpInfo = ({ info }) => {


  // const filteredKey = keyInfo.map(keys => (
  //   keys.filteredKey()
  // ))

  const keyInfo = Object.entries(info);

  return (
    <div className="container">


      {info.error
        ? <p className="simple-text">
          Something went wrong üòû. Please try again.
        </p>
        : <ul className="list">
          {
            keyInfo.map(keys => {
            if (typeof (keys[1]) === 'string') {
              return (
                <li className={`list-item list-item--${typeof (keys[1])}`}>
                  {`${keys[0]} `}
                  <span className={typeof (keys[1])}>
                    {`"${keys[1]}"`}
                  </span>
                </li>
              )
            }

            if (typeof (keys[1]) === 'boolean') {
              return (
                <li className={`list-item list-item--${typeof (keys[1])}`}>
                  {`${keys[0]} `}
                  <span className={typeof (keys[1])}>
                    {keys[1]
                      ? 'true'
                      : 'false'
                    }
                  </span>
                </li>
              )
            }

            if (typeof (keys[1]) === 'number') {
              return (
                <li className={`list-item list-item--${typeof (keys[1])}`}>
                  {`${keys[0]} `}
                  <span className={typeof (keys[1])}>
                    {keys[1]}
                  </span>
                </li>
              )
            }

            return ''
          })}
        </ul>
      }
    </div>
  )
}